.coding-page {
	// background: $color-white
	height: 100%;
	overflow: auto;
	header {
		position: fixed;
		width: 100%;
		z-index: 100;
		@include media-breakpoint-up(md) {
			&.active {
				.navbar {
					background: rgba(255,255,255,.2);
					@include box-shadow(0, 1px, 0px, rgba(255,255,255,.3));
					#BNGlobalMenu {
						.navbar-brand {
							i {
								svg {
									* {
										fill: $color-white; } }
								&:before {
									background-color: $color-white; } } }
						.nav.navbar-right {
							.nav-item {
								.nav-link {
									color: rgba(255,255,255,.5); }
								&.active, &:hover {
									.nav-link {
										color: $color-white;
										&:before {
											background-color: $color-white;
											bottom: -0.65rem;
											top: initial; } } } } } } } } } }
	main {
		@include mar(7rem,top);
		max-height: 100%;
		color: $color-con3;
		@include media-breakpoint-up(md) {
			@include pad(0 4rem); }
		.text-replace {
			white-space: pre;
			line-height: 1.25em; } } }


